import React, { Component } from "react";
import { withStyles } from "@material-ui/core/styles";
import "./App.css";


import socketIOClient from "socket.io-client";
import CircularProgress from "@material-ui/core/CircularProgress";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Paper from "@material-ui/core/Paper";

import Oversikt from "./components/Oversikt";
import Spillere from "./components/Spillere";
import Clients from "./components/Clients";
import Progresjon from "./components/Progresjon";
import Typography from "@material-ui/core/Typography";

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

const photo = require('./logo.png');

const theme = createMuiTheme({
   
});
const TabContainer = props => {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
};

const styles = theme => ({
  root: {
    width: "100%",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100%"
  },
  paper: {
    flexGrow: 1,
    alignSelf: "flex-start",
    overflowX: "auto",
    width: "80%",
    minHeight: '90%',
    zIndex: '1'
  },

  progress: {
    textAlign: "center",
    color: "#ffffff",
    margin: theme.spacing.unit * 2
  },
  appbarcont: {
    display: "flex",
    justifyContent: "flex-start",
    alignItems: "flex-start",
  },
  logo: {
    position: 'fixed',
    bottom: '1rem',
    right: '1rem',
    width: '72px',
    zIndex: '0',
    opacity: '0.1'
  }
});

class App extends Component {
  constructor() {
    super();

    console.log(process.env.NODE_ENV);

    this.state = {
      tabIndex: 0,
      gameMode: "classvisit",
      response: false,
      currentState: false,
      endpoint: "http://gs-alta.a3c1.starter-us-west-1.openshiftapps.com"
    };

    this.handleCurrentStateChange = this.handleCurrentStateChange.bind(this);
    this.handleGameModeChange = this.handleGameModeChange.bind(this);
  }

  handleTabChange = (event, value) => {
    this.setState({ tabIndex: value });
  };

  handleCurrentStateChange(newState) {
    this.setState({ currentState: newState });
  }

  handleGameModeChange(event, newGameMode) {
    console.log(newGameMode);
    this.setState({ gameMode: newGameMode });
  }

  componentDidMount() {
    const { endpoint } = this.state;
    const socket = socketIOClient(endpoint);

    if (socket) {
      socket.on("currentState message", this.handleCurrentStateChange);
    }
  }

  render() {
    const { classes } = this.props;
    const { currentState, tabIndex} = this.state;

    const loadingBar = (
      <CircularProgress className={classes.progress} size={50} />
    );

    return (
       <MuiThemeProvider theme={theme}>
      <div className={classes.root}>
        {!currentState && loadingBar}

        {currentState && (
          <Paper className={classes.paper}>
            <AppBar position="static">
            <div className={classes.appbarcont}>
              
              <Tabs value={tabIndex} onChange={this.handleTabChange}>
                <Tab label="Oversikt" href="#oversikt" />
                <Tab label="Klienter" href="#klienter" />
                <Tab label="Spillere" href="#spillere" />
                <Tab label="Progresjon" href="#Progresjon" />
              </Tabs>
              </div>
            </AppBar>
            {tabIndex === 0 && (
              <TabContainer>
                <Oversikt currentState={currentState} />
              </TabContainer>
            )}
            {tabIndex === 1 && (
              <TabContainer>
                <Clients currentState={currentState} />
              </TabContainer>
            )}
            {tabIndex === 2 && (
              <TabContainer>
                <Spillere currentState={currentState} />
              </TabContainer>
            )}
            {tabIndex === 3 && (
              <TabContainer>
                <Progresjon currentState={currentState} />
              </TabContainer>
            )}
          </Paper>
        )}
        <img alt="logo" src={photo} className={classes.logo}/>
      </div>
      </MuiThemeProvider>
    );
  }
}

export default withStyles(styles)(App);



