import React, {Component} from "react";


import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";

import Gruppeprogresjon from './GruppeProgresjon';


const styles = theme => ({
  groups: {
    flexFlow: 'row nowrap',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
    width: '100%',
    textAlign: 'center'
  }
});


export class Progresjon extends Component {
  constructor() {
    super();
    this.state = {activeStep : 0, completed: []};
  }

  render() {
  	const { classes } = this.props;
   
    return (

    	<div className={classes.groups}>
    		<Gruppeprogresjon group="Gruppe 1" />
    		<Gruppeprogresjon group="Gruppe 2" />
    		<Gruppeprogresjon group="Gruppe 3" />
    	</div>

    );
  }
}

Progresjon.propTypes = {
  currentState: PropTypes.object
};


export default withStyles(styles)(Progresjon);


