import React, {Component} from "react";



import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";

import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';

import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import StepButton from '@material-ui/core/StepButton';
const styles = theme => ({
  gruppeProg: {
    display: 'inline-block',
    width: '350px'
  }
});

function getSteps() {
  return ['Registrering', 'DEL #1', 'DEL #2', 'DEL #3', 'FERDIG'];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return `For each ad campaign that you create, you can control how much
              you're willing to spend on clicks and conversions, which networks
              and geographical locations you want your ads to show on, and more.`;
    case 1:
      return 'An ad group contains one or more ads which target a shared set of keywords.';
    case 2:
      return `Try out different ad text to see what brings in the most customers,
              and learn how to enhance your ads using features like ad extensions.
              If you run into any problems with your ads, find out how to tell if
              they're running and how to resolve approval issues.`;
    default:
      return 'Unknown step';
  }
}


export class Progresjon extends Component {
  constructor() {
    super();
    this.state = {activeStep : 0, completed: []};
  }


  handleNext = () => {
    this.setState(state => ({
      activeStep: state.activeStep + 1,
    }));
  };

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
    }));
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };

  handleStep = step => () => {
    this.setState({
      activeStep: step,
    });
  };

  render() {
  	const { classes, group } = this.props;
  	 const steps = getSteps();
    const { activeStep } = this.state;

    return (
    	<div className={classes.gruppeProg}>

    	<div>
    		{group}
    	</div>

 <Stepper activeStep={activeStep} orientation="vertical" nonLinear >
          {steps.map((label, index) => {
            return (
              <Step key={label}>
              	<StepButton
                  onClick={this.handleStep(index)}
                  completed={this.state.completed[index]}>
                  {label}
                </StepButton>
                <StepContent>
                  <Typography>{getStepContent(index)}</Typography>

                  <div className={classes.actionsContainer}>
                    <div>
                      <Button
                        disabled={activeStep === 0}
                        onClick={this.handleBack}
                        className={classes.button}
                      >
                        Back
                      </Button>
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={this.handleNext}
                        className={classes.button}
                      >
                        {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                      </Button>
                    </div>
                  </div>

                </StepContent>
              </Step>
            );
          })}
        </Stepper>
        {activeStep === steps.length && (
          <Paper square elevation={0} className={classes.resetContainer}>
            <Typography>All steps completed - you&quot;re finished</Typography>
            <Button onClick={this.handleReset} className={classes.button}>
              Reset
            </Button>
          </Paper>
        )}

		</div>
    );
  }
}

Progresjon.propTypes = {
  currentState: PropTypes.object
};


export default withStyles(styles)(Progresjon);


