import React, {Component} from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";

const styles = theme => ({
  table: {
    minWidth: 700
  },
  column: {
    textAlign: "left"
  }
});


export class Spillere extends Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    const { classes } = this.props;
    const { currentState } = this.props;

    return (
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell>Client ID</TableCell>
            <TableCell>State</TableCell>
            <TableCell>Status</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {currentState.clients.map(row => {
            return (
              <TableRow key={`client-${row.id}`}>
                <TableCell className={classes.column}>{row.id}</TableCell>
                <TableCell className={classes.column}>{row.state}</TableCell>
                <TableCell className={classes.column}>{row.status}</TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
    );
  }
}

Spillere.propTypes = {
  currentState: PropTypes.object
};


export default withStyles(styles)(Spillere);


